
.register-page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: url(../register-img.jpg) no-repeat center center/cover;
    position: relative;
  }
  
  .register-page::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5); /* Overlay to darken background image */
    z-index: 1;
  }
  
  .register-page .ant-form {
    z-index: 2;
    background: #ffffff;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    width: 400px;
  }
  
  .register-page h1 {
    text-align: center;
    margin-bottom: 20px;
    color: #333333;
  }
  
  .register-page .ant-form-item-label > label {
    color: #333333;
  }
  
  .register-page .ant-input {
    border-radius: 5px;
  }
  
  .register-page .btn-primary {
    background-color: #1890ff;
    border: none;
    color: white;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 5px;
  }
  
  .register-page .btn-primary:hover {
    background-color: #40a9ff;
  }
  
  .register-page .d-flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
  }
  
  .register-page a {
    color: #1890ff;
  }
  
  .register-page a:hover {
    color: #40a9ff;
  }
  
  .link-register{
    padding: 10px;
  }

  .vertical-container {
    display: flex;
    flex-direction: column; /* Stack items vertically */
    align-items: center; /* Center items horizontally */
    justify-content: center; /* Center items vertically */
    height: 15vh; /* Full height of the viewport */
  }