
.login-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: url(../login-img.jpg) no-repeat center center/cover;
  position: relative;
}

.login-page::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5); /* Overlay to darken background image */
  z-index: 1;
}

.login-page .ant-form {
  z-index: 2;
  background: #ffffff;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.login-page h1 {
  text-align: center;
  margin-bottom: 20px;
  color: #333333;
}

.login-page .ant-form-item-label > label {
  color: #333333;
}

.login-page .ant-input {
  border-radius: 5px;
}

.login-page .btn-primary {
  background-color: #1890ff;
  border-color: #1890ff;
  border-radius: 5px;
}

.login-page .btn-primary:hover {
  background-color: #40a9ff;
  border-color: #40a9ff;
}

.login-page .d-flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}

.login-page a {
  color: #1890ff;
}

.login-page a:hover {
  color: #40a9ff;
}

.link{
  padding: 20px;
}

.vertical-container {
  display: flex;
  flex-direction: column; /* Stack items vertically */
  align-items: center; /* Center items horizontally */
  justify-content: center; /* Center items vertically */
  height: 15vh; /* Full height of the viewport */
}